                <div class="p-md-6 p-field p-grid">
                    <label class="p-col-fixed p-mb-0">Fate</label>
                    <div class="p-col">
                        <p-dropdown
                            v-model="embryo.fate"
                            :options="fateOptions"
                            optionLabel="label"
                            optionValue="value"
                            style="width: 120px"
                        />
                    </div>
                </div>

                <div class="p-md-6 p-field p-grid">
                    <label class="p-col-fixed p-mb-0 p-ml-4">
                        Thawing Date
                    </label>
                    <div class="p-col">
                        <p-calendar
                            v-model="embryo.thawingDate"
                            :showIcon="true"
                            dateFormat="yy-mm-dd"
                            :class="{
                                'p-invalid': embryoDetailV$.thawingDate.$invalid
                            }"
                        />
                        <small
                            v-if="embryoDetailV$.thawingDate.validator.$invalid"
                            class="p-error"
                            >{{ embryoDetailV$.thawingDate.validator.$message }}
                        </small>

                        <small
                            v-if="embryoDetailV$.thawingDate.validDate.$invalid"
                            class="p-error"
                            >{{ embryoDetailV$.thawingDate.validDate.$message }}
                        </small>
                    </div>
                </div>

                <div class="p-md-6 p-field p-grid">
                    <label class="p-col-fixed p-mb-0">Frozen Date</label>
                    <div class="p-col">
                        <p-calendar
                            v-model="embryo.frozenDate"
                            :showIcon="true"
                            dateFormat="yy-mm-dd"
                            :class="{
                                'p-invalid': embryoDetailV$.frozenDate.$invalid
                            }"
                        />
                        <small
                            v-if="embryoDetailV$.frozenDate.validator.$invalid"
                            class="p-error"
                            >{{ embryoDetailV$.frozenDate.validator.$message }}
                        </small>

                        <small
                            v-if="embryoDetailV$.frozenDate.validDate.$invalid"
                            class="p-error"
                            >{{ embryoDetailV$.frozenDate.validDate.$message }}
                        </small>
                    </div>
                </div>

                <div class="p-md-6 p-field p-grid">
                    <label class="p-col-fixed p-mb-0">Discarding Date</label>
                    <div class="p-col">
                        <p-calendar
                            v-model="embryo.discardingDate"
                            :showIcon="true"
                            dateFormat="yy-mm-dd"
                            :class="{
                                'p-invalid':
                                    embryoDetailV$.discardingDate.$invalid
                            }"
                        />
                        <small
                            v-if="
                                embryoDetailV$.discardingDate.validator.$invalid
                            "
                            class="p-error"
                        >
                            {{
