                            }}
                        </small>

                        <small
                            v-if="
                                embryoDetailV$.discardingDate.validDate.$invalid
                            "
                            class="p-error"
                        >
                            {{
                                embryoDetailV$.discardingDate.validDate.$message
                            }}
                        </small>
                    </div>
                </div>

                <div class="p-md-6 p-field p-grid">
                    <label class="p-col-fixed p-mb-0">Transfer Date</label>
                    <div class="p-col">
                        <p-calendar
                            v-model="embryo.transferDate"
                            :showIcon="true"
                            dateFormat="yy-mm-dd"
                        />
                    </div>
                </div>

                <!-- <div
                    class="p-md-6 p-field p-grid"
                    v-if="
                        embryo.fate == 'Transfer' &&
                            selectedEmbryo.cycleType == 'FET'
                    "
                > -->
                <div
                    class="p-md-6 p-field p-grid"
                    v-if="embryo.fate == 'Transfer' || embryo.fate == 'Discard'"
                >
                    <label class="p-col-fixed p-mb-0">Straw</label>
                    <div class="p-col">
                        <p-input-text
                            v-model="embryo.straw"
                            :class="{
                                'p-invalid': embryoDetailV$.straw.$invalid
                            }"
                        />
                        <br />
                        <small
                            v-if="
                                (embryoDetailV$.straw.$invalid &&
                                    embryoDetailSubmitted) ||
                                    embryoDetailV$.straw.$pending.$response
                            "
                            class="p-error"
                            >{{
                                embryoDetailV$.straw.validator.$message.replace(
                                    "Value",
                                    "Straw ID"
                                )
                            }}
                        </small>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { computed, inject, onMounted, reactive, ref, watch } from "vue";
import { useStore } from "vuex";
import { FATE_TYPE, TIME_0 } from "@/constants";

export default {
    setup() {
        const store = useStore();
        const embryo = inject("embryoDetail");
        const selectedEmbryo = inject("selectedEmbryo");
        const fateOptions = computed(() => store.state.options.fateList);
        const embryologistOptions = computed(
            () => store.state.doctor.embryologists
        );

        const cryoStorageVisible = computed(
            () => embryo.fate == "Frozen" || embryo.fate == "Thawed"
        );

        const cyroStorage = inject("cyroStorage");
        const v$ = inject("cyroStorageV$");
        const submitted = inject("cyroStorageSubmitted");

        const pugColorOptions = computed(
            () => store.state.cryoStorage.pugColorList
        );

        const cassetteTypeOptions = computed(
            () => store.state.cryoStorage.cassetteTypeList
        );

        const cassetteColorOptions = computed(
            () => store.state.cryoStorage.cassetteColorList
        );

        const dewarOptions = ref([
            "Thermo",
            "Vapor",
            "1",
            "2",
            "3",
            "4",
            "5",
            "6",
            "7",
            "8"
        ]);

        const embryoDetailV$ = inject("embryoDetailV$");
        const embryoDetailSubmitted = inject("embryoDetailSubmitted");

        watch(
            () => embryo.fate,
            (newVal) => {
                if (newVal == FATE_TYPE.FROZEN) {
                    if (embryo.frozenDate == null)
                        embryo.frozenDate = new Date(
                            selectedEmbryo.date + TIME_0
                        );
                    embryo.discardingDate = null;
                    embryo.thawingDate = null;
                    embryo.transferDate = null;
                    return;
                } else if (newVal == FATE_TYPE.DISCARD) {
                    if (embryo.discardingDate == null)
                        embryo.discardingDate = new Date(
                            selectedEmbryo.date + TIME_0
                        );
                    embryo.frozenDate = null;
                    embryo.thawingDate = null;
                    embryo.transferDate = null;
                    return;
                } else if (newVal == FATE_TYPE.THAWED) {
                    if (embryo.thawingDate == null) {
                        embryo.thawingDate = new Date();
                        // embryo.thawingDate = new Date(
                        //     selectedEmbryo.date + TIME_0
                        // );
                    }

                    embryo.frozenDate = null;
                    embryo.discardingDate = null;
                    embryo.transferDate = null;
                    return;
                } else if (newVal == FATE_TYPE.TRANSFER) {
                    if (embryo.transferDate == null)
                        embryo.transferDate = new Date(
                            selectedEmbryo.date + TIME_0
                        );
                    embryo.frozenDate = null;
                    embryo.thawingDate = null;
                    embryo.discardingDate = null;
                    return;
                } else {
                    embryo.transferDate = null;
                    embryo.frozenDate = null;
                    embryo.thawingDate = null;
                    embryo.discardingDate = null;
                    return;
                }
            }
        );

        onMounted(() => {
            submitted.value = false;
        });

        return {
            embryo,
            selectedEmbryo,
            fateOptions,
            embryologistOptions,
            cyroStorage,
            v$,
            submitted,
            cryoStorageVisible,
            pugColorOptions,
            cassetteTypeOptions,
            cassetteColorOptions,
            dewarOptions,
            embryoDetailV$,
            embryoDetailSubmitted
        };
    }
};
</script>

<style scoped>
.p-field-checkbox,
.p-field-radiobutton {
    margin-bottom: 0;
}
</style>
