<template>
    <div class="p-grid p-input-filled" style="">
        <p-scroll-panel
            class="p-mb-2"
            style="width: 1420px; height: calc(100vh - 400px);"
        >
            <div class="p-grid">
                <div class="p-col-fixed gird-header" style="width:40px"></div>

                <div class="p-col-fixed gird-header" style="width:140px;">
                    <div class="p-d-flex p-jc-between">
                        <strong>Occ.no.</strong>
                        <p-button
                            :icon="
                                gridDesc
                                    ? 'pi pi-sort-numeric-up'
                                    : 'pi pi-sort-numeric-down'
                            "
                            class="p-button-text p-button-secondary p-button-sm"
                            style="
                                padding: 0;
                                font-size: 1.2rem;
                                color: black;
                            "
                            @click="sortToggle"
                        />
                    </div>
                </div>
                <template
                    v-for="(item, index) in fertilizationGridDays"
                    :key="index"
                >
                    <div
                        class="p-col-fixed gird-header"
                        style="width:120px"
                        v-if="item.day != 2"
                    >
                        <div class="p-d-flex p-jc-center">
                            <span class="day-title">Day {{ item.day }}</span>
                        </div>
                    </div>
                </template>
                <div class="p-col-fixed gird-header" style="width:78px"></div>
                <div class="p-col-fixed gird-header" style="width:100px"></div>
                <div class="p-col-fixed gird-header" style="width:80px"></div>
                <div class="p-col-fixed gird-header" style="width:120px"></div>
            </div>

            <div class="p-grid">
                <div class="p-col-fixed gird-header" style="width:40px">
                    <div
                        class="p-d-flex p-jc-center p-ai-center"
                        style="height: 25px"
                    >
                        <p-checkbox v-model="checkAll" :binary="true" />
                    </div>
                </div>

                <div class="p-col-fixed gird-header" style="width:140px">
                    <div
                        class="p-d-flex p-jc-between p-ai-center"
                        style="height: 25px"
                    >
                        <strong>Occ ID</strong>
                        <span>|</span>
                        <span>Treat</span>
                    </div>
                </div>
                <template
                    v-for="(item, index) in fertilizationGridDays"
                    :key="index"
                >
                    <div
                        class="p-col-fixed gird-header"
                        style="width:120px"
                        v-if="item.day != 2"
                    >
                        <div class="p-inputgroup">
                            <p-calendar
                                v-model="item.date"
                                :disabled="!item.editable"
                                dateFormat="yy-mm-dd"
                            />
                            <p-button
                                v-show="!item.editable"
                                icon="pi pi-pencil"
                                class="p-button-text"
                                @click="item.editable = true"
                            />
                            <p-button
                                v-show="item.editable"
                                icon="pi pi-save"
                                class="p-button-text"
                                @click="updateFertilization(item)"
                            />
                        </div>
                    </div>
                </template>

                <div class="p-col-fixed gird-header" style="width:78px">
                    <div
                        class="p-d-flex p-jc-center p-ai-center"
                        style="height: 25px"
                    >
                        Fate
                    </div>
                </div>

                <div class="p-col-fixed gird-header" style="width:100px">
                    Frozen / <br />Thawing date
                </div>

                <div class="p-col-fixed gird-header" style="width:80px">
                    Straw ID
                </div>

                <div class="p-col-fixed gird-header" style="width:120px">
                    <div
                        class="p-d-flex p-jc-center p-ai-center"
                        style="height: 25px; font-weight: bold;"
                    >
                        PGT
                    </div>
                </div>
            </div>

            <div
                class="p-grid"
                v-for="(line, index) in fertilizationGrid"
                :key="index"
                :style="{
                    'background-color': getFertilizationColor(line.fate)
                }"
            >
                <div
                    class="p-col-fixed"
                    style="width:40px; border-right: 1px solid lightgray"
                >
                    <div class="p-d-flex p-jc-center p-ai-center">
                        <p-checkbox v-model="checkLine[index]" :binary="true" />
                        <!-- <p-button
                            icon="pi pi-pencil"
                            class="p-button-text p-button-secondary"
                            @click="editFetilizationRow(line)"
                        /> -->
                    </div>
                </div>

                <div
                    class="p-col-fixed"
                    style="width:140px; border-left: 1px solid lightgray"
                >
                    <div class="p-d-flex p-jc-between">
                        <strong>{{ line.noEmbryo }}</strong>
                        <!-- <strong
                            class="fate-text"
                            :style="{ backgroundColor: line.color }"
                        >
                            {{ line.fate ? line.fate.substring(0, 2) : "" }}
                        </strong> -->
                    </div>
                    <div class="p-d-flex p-jc-between p-mt-2">
                        <span>{{ line.lineId }}</span>
                        <strong style="font-size: 1rem">
                            {{ line.cycleType }}
                        </strong>
                    </div>
                </div>
                <template
                    v-for="(item, index) in fertilizationGridDays"
                    :key="index"
                >
                    <div
                        class="p-col-fixed"
                        style="width:120px"
                        v-if="item.day != 2 && item.day == 0"
                        :class="{
                            'selected-grid':
                                item.day == selectedEmbryo.day &&
                                line.lineId == selectedEmbryo.fertLineId
                        }"
                    >
                        <div
                            class="p-d-flex p-flex-column p-jc-center p-ai-center"
                        >
                            <template v-if="line.maturity">
                                <p-button
                                    :label="line.maturity"
                                    class="p-button-secondary p-button-text p-button-lg cursor-pointer"
                                    @click="onGridCellClick(item, line)"
                                    @dblclick="
                                        openEmbryoDetailDialog(item, line)
                                    "
                                    style="
                                        font-size: 1.25rem;
                                        font-weight: bold;
                                        color: black;
                                    "
                                />
                                <!-- <strong
                                    style="padding-top: 4px"
                                    :style="{ color: line.color }"
                                >
                                    {{ showFateInGrid(line, item) }}
                                </strong> -->
                            </template>

                            <template v-else>
                                <p-avatar
                                    class="cursor-pointer"
                                    style="font-size: 1.25rem;
                                         font-weight: bold;
                                         border: solid 2px white;
                                         width: 100%;
                                        "
                                    shape="rect"
                                    size="large"
                                    @click="onGridCellClick(item, line)"
                                    @dblclick="
                                        openEmbryoDetailDialog(item, line)
                                    "
                                />
                            </template>
                        </div>
                    </div>
                    <div
                        class="p-col-fixed"
                        style="width:120px"
                        v-if="item.day != 2 && item.day != 0"
                        :class="{
                            'selected-grid':
                                item.day == selectedEmbryo.day &&
                                line.lineId == selectedEmbryo.fertLineId
                        }"
                    >
                        <div
                            class="p-d-flex p-flex-column p-jc-center p-ai-center"
                        >
                            <template
                                v-if="
                                    [5, 6, 7].includes(item.day) &&
                                        line[`day${item.day}SG`]
                                "
                            >
                                <p-button
                                    :label="line[`day${item.day}SG`]"
                                    class="p-button-secondary p-button-text p-button-lg fert cursor-pointer"
                                    @click="onGridCellClick(item, line)"
                                    @dblclick="
                                        openEmbryoDetailDialog(item, line)
                                    "
                                    style="
                                        font-size: 1.25rem;
                                        font-weight: bold;
                                        color: black;
                                    "
                                />
                                <!-- <strong
                                    style="padding-top: 4px"
                                    :style="{ color: line.color }"
                                >
                                    {{ showFateInGrid(line, item) }}
                                </strong> -->
                            </template>

                            <template v-else-if="line[`day${item.day}Stage`]">
                                <p-button
                                    :label="line[`day${item.day}Stage`]"
                                    class="p-button-secondary p-button-text p-button-lg fert cursor-pointer"
                                    @click="onGridCellClick(item, line)"
                                    @dblclick="
                                        openEmbryoDetailDialog(item, line)
                                    "
                                    style="
                                        font-size: 1.25rem;
                                        font-weight: bold;
                                        color: black;
                                    "
                                />
                                <!-- <strong
                                    style="padding-top: 4px"
                                    :style="{ color: line.color }"
                                >
                                    {{ showFateInGrid(line, item) }}
                                </strong> -->
                            </template>

                            <template v-else>
                                <p-avatar
                                    class="cursor-pointer"
                                    style="font-size: 1.25rem;
                                         font-weight: bold;
                                         border: solid 2px white;
                                         width: 100%;
                                        "
                                    shape="rect"
                                    size="large"
                                    @dblclick="
                                        openEmbryoDetailDialog(item, line)
                                    "
                                />
                            </template>
                        </div>
                    </div>
                </template>

                <div
                    class="p-col-fixed"
                    style="width:79px; border-right:1px solid lightgray"
                >
                    <div class="p-d-flex p-jc-center p-mt-2">
                        {{ line.fate }}
                    </div>
                </div>

                <div
                    class="p-col-fixed"
                    style="width:100px; border-right:1px solid lightgray"
                >
                    <!-- <div
                        v-if="line.fate == 'Thawed'"
                        class="p-d-flex p-jc-center p-mt-2"
                    >
                        {{ line.thawDate }}
                    </div>

                    <div v-else class="p-d-flex p-jc-center p-mt-2">
                        {{ line.frozenDate }}
                    </div> -->

                    <div class="p-d-flex p-jc-center p-mt-2">
                        {{ line.fateDate }}
                    </div>
                </div>

                <div class="p-col-fixed" style="width:80px">
                    <div class="p-d-flex p-jc-center p-mt-2">
                        {{ line.strawId }}
                    </div>
                </div>

                <div class="p-col-fixed" style="width:120px">
                    <p-textarea
                        v-model="line.comment"
                        rows="2"
                        style="width: 95%; max-width: 95%; max-height: 90%;"
                        @blur="saveComment(line)"
                    />
                </div>
            </div>
        </p-scroll-panel>

        <div class="p-fluid p-formgrid p-grid p-input-filled">
            <div class="p-field p-col-fixed">
                <label class="p-mb-1">Comment</label>
                <p-textarea v-model="aspirationForm.comment" />
            </div>
            <div class="p-field p-col-fixed">
                <label class="p-mb-1">Exp Comment</label>
                <p-textarea v-model="aspirationForm.expComment" />
            </div>

            <div class="p-field p-col-fixed">
                <p-button
                    label="Save"
                    @click="save()"
                    style="margin-top: 30px;"
                    v-if="hasUpdatePermission"
                />
            </div>
            <div class="p-field p-col-fixed">
                <p-button
                    label="Create"
                    @click="createFetilizationRow()"
                    style="margin-top: 30px;"
                    v-if="hasCreatePermission"
                />
            </div>
            <div class="p-field p-col-fixed">
                <p-button
                    label="Report"
                    style="margin-top: 30px;"
                    @click="openPdfReport()"
                />
            </div>
            <div class="p-field p-col-fixed">
                <p-button
                    label="Edit"
                    class="p-button-secondary"
                    style="margin-top: 30px;"
                    @click="editFetilizationRows"
                    v-if="isChceked"
                />
            </div>
            <div class="p-field p-col-fixed">
                <p-button
                    label="Delete"
                    class="p-button-danger"
                    style="margin-top: 30px;"
                    @click="deleteLines"
                    v-if="isChceked"
                />
            </div>
        </div>

        <fertilization-line-create-edit-dailog />
    </div>
</template>

<script>
import FertilizationLineCreateEditDailog from "@/components/fertilization/line-create-edit-dialog.vue";

import { useFertilizationGridInit } from "@/composable/fertilization";
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    provide,
    watch,
    ref
} from "vue";
import { emitter } from "@/event-bus";
import { usePermissionCheck } from "@/composable/users";
import { FERTILIZATION_COLOR } from "@/config";

export default {
    components: { FertilizationLineCreateEditDailog },
    setup() {
        const {
            store,
            fertilizationGrid,
            sortToggle,
            updateFertilization,
            gridDesc,
            createFetilizationRow,
            fertilizationGridDays,
            moveGridCell,
            openEmbryoDetailDialog,
            closeEmbryoDetailDialog,
            fillDaysRow,
            createFertilizationLineDialog,
            editFetilizationRow,
            ferilizationLine,
            showFateInGrid,
            onGridCellClick,
            selectedEmbryo,
            openPdfReport,
            checkAll,
            checkLine,
            deleteLines,
            editFetilizationRows,
            isFertilizationEdit,
            selectedLines
        } = useFertilizationGridInit();

        watch(fertilizationGrid, (newVal) => {
            checkLine.value = newVal.map((_) => false);
        });

        watch(checkAll, (newVal) => {
            if (newVal) {
                checkLine.value = checkLine.value.map((_) => true);
            } else {
                checkLine.value = checkLine.value.map((_) => false);
            }
        });

        const isChceked = computed(() => {
            return checkLine.value.filter((item) => item).length != 0;
        });

        const {
            hasUpdatePermission,
            hasCreatePermission
        } = usePermissionCheck();

        provide("createFertilizationLineDialog", createFertilizationLineDialog);
        provide("ferilizationLine", ferilizationLine);
        provide("isFertilizationEdit", isFertilizationEdit);
        provide("selectedLines", selectedLines);

        const getFertilizationColor = (fate) => {
            if (fate === "Frozen") return FERTILIZATION_COLOR.FROZEN;
            if (fate === "Discard") return FERTILIZATION_COLOR.DISCARD;
            if (fate === "Transfer") return FERTILIZATION_COLOR.TRANSFER;
            else return "#F6F6F6";
        };

        const aspirationForm = inject("aspirationForm");

        const saveComment = (line) => {
            let fertilization = store.state.fertilization.fertilization;

            store
                .dispatch("fertilization/createFertilizationLine", {
                    ...line,
                    fertilizationId: fertilization.fertId
                })
                .then(() => {})
                .catch(() => {
                    console.log("err");
                })
                .finally(() => {
                    createFertilizationLineDialog.value = false;
                    loading.value = false;
                });
        };

        watch(() => store.state.fertilization.fertilization, fillDaysRow);

        onMounted(() => {
            store.commit("fertilization/EMPTY_FERTILIZAION_LINE");
            emitter.on("embyro-detail-dialog:move-cell", moveGridCell);
        });

        onUnmounted(() => {
            emitter.off("embyro-detail-dialog:move-cell", moveGridCell);
        });

        return {
            fertilizationGrid,
            sortToggle,
            gridDesc,
            updateFertilization,
            createFetilizationRow,
            fertilizationGridDays,
            openEmbryoDetailDialog,
            closeEmbryoDetailDialog,
            editFetilizationRow,
            showFateInGrid,
            onGridCellClick,
            selectedEmbryo,
            openPdfReport,
            hasUpdatePermission,
            hasCreatePermission,
            getFertilizationColor,
            save: () => {
                emitter.emit("cycle-monitoring:aspiration-form-save");
            },
            saveComment,
            aspirationForm,
            checkAll,
            checkLine,
            deleteLines,
            editFetilizationRows,
            isChceked
        };
    }
};
</script>

<style scoped>
.p-grid {
    margin-top: 1px;
}
.p-col-fixed {
    margin-left: 1px;
    padding: 2px;
}
:deep() .p-scrollpanel-content {
    padding: 10px;
}
.cursor-pointer {
    cursor: pointer;
}
.gird-header {
    background-color: lightblue;
}
.day-title {
    font-weight: bold;
    font-size: 14px;
}
.p-calendar :deep() .p-inputtext[disabled] {
    background-color: transparent;
}
.p-avatar {
    background-color: transparent;
}
.fate-text {
    padding: 2px 8px 2px 8px;
    color: white;
    font-size: 12px;
    border-radius: 20px;
}
.selected-grid {
    background-color: aqua;
}
.frozen {
    background-color: aqua;
}
.discard {
    background-color: greenyellow;
}
.tranfer {
    background-color: red;
}
</style>
