<template>
    <p-dialog
        header="Confirmation!"
        v-model:visible="embryoDetailCreateConfirmDailog"
        :modal="true"
        style="width: 400px"
    >
        <div class="p-grid p-mb-2">
            <div class="p-col-12">
                Are you sure you want to create new?
            </div>
        </div>

        <div class="p-grid">
            <label class="p-col-fixed p-mt-2">How many?</label>
            <div class="p-col-fixed">
                <p-input-number v-model="howMany" />
            </div>
        </div>

        <template #footer>
            <p-button
                label="No"
                icon="pi pi-times"
                class="p-button-text"
                @click="hide"
            />
            <p-button
                label="Yes"
                icon="pi pi-check"
                autofocus
                @click="onOkHandler"
            />
        </template>
    </p-dialog>
</template>

<script>
import { ref, inject } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();

        const embryoDetailCreateConfirmDailog = inject(
            "embryoDetailCreateConfirmDailog"
        );

        const howMany = ref();

        const onOkHandler = () => {
            store
                .dispatch("fertilization/copyFertilizationLine", howMany.value)
                .then(() => {})
                .catch(() => {})
                .finally(() => {
                    embryoDetailCreateConfirmDailog.value = false;
                });
        };

        return {
            embryoDetailCreateConfirmDailog,
            howMany,
            hide: () => {
                embryoDetailCreateConfirmDailog.value = false;
            },
            onOkHandler
        };
    }
};
</script>

<style scoped>
:deep() .p-inputtext {
    width: 50px;
}
</style>
