<template>
    <p-card class="p-mt-1">
        <template #content>
            <p-divider align="left" class="p-mt-0">
                <b>Embryology - Frozen Embryo</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <!-- <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Oocyte Collection Date
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.oocyteCollectionDate"
                                dateFormat="yy-mm-dd"
                                :showIcon="true"
                            />
                        </div>
                    </div>
                </div> -->
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #1 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID1Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #2 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID2Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #3 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID3Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #4 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID4Thawed"
                            />
                        </div>
                    </div>
                </div>
                <!-- <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="">
                            Freeze Id #4 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <div class="p-formgroup-inline">
                                <div class="p-field-checkbox">
                                    <p-radio-button value="1" />
                                    <label>Yes</label>
                                </div>
                                <div class="p-field-checkbox">
                                    <p-radio-button value="0" />
                                    <label>NO</label>
                                </div>
                            </div>
                        </div>
                    </div>
                </div> -->
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # embryo thawed
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.embryosThawed"
                                :max="99"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # utilizable embryos after thaw:
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.utilizableAfterThaw"
                                :max="99"
                                :class="{
                                    'p-invalid': v$.utilizableAfterThaw.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.utilizableAfterThaw.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.utilizableAfterThaw.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Thawed Embryo stage at Freezing
                        </label>
                        <div class="p-col">
                            <p-multi-select
                                v-model="form.embryoStageThawing"
                                :options="embryoStageThawingOptions"
                                optionLabel="label"
                                optionValue="value"
                                style="width: 250px"
                                :class="{
                                    'p-invalid': v$.embryoStageThawing.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoStageThawing.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoStageThawing.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Thawed Embryo cryo method
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.embryoFrozenCryoMethod"
                                :options="embryoFrozenCryoMethodOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid':
                                        v$.embryoFrozenCryoMethod.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoFrozenCryoMethod.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFrozenCryoMethod.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Thaw Date
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.embryoThawDate"
                                dateFormat="mm/dd/yy"
                                :showIcon="true"
                                :class="{
                                    'p-invalid': v$.embryoThawDate.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoThawDate.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoThawDate.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-mt-1">
                    <div class="p-formgroup-inline">
                        <label for="" class="p-mt-2 p-mr-2">
                            Donated Embryo From Another Couple
                        </label>
                        <div class="p-field-checkbox">
                            <p-radio-button
                                name="hospitalization"
                                value="1"
                                v-model="form.donatedEmbryoFromAnotherCouple"
                                :class="{
                                    'p-invalid':
                                        v$.donatedEmbryoFromAnotherCouple
                                            .$invalid
                                }"
                            />
                            <label>Yes</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-radio-button
                                name="hospitalization"
                                value="0"
                                v-model="form.donatedEmbryoFromAnotherCouple"
                                :class="{
                                    'p-invalid':
                                        v$.donatedEmbryoFromAnotherCouple
                                            .$invalid
                                }"
                            />
                            <label>NO</label>
                        </div>
                        <i
                            v-if="v$.donatedEmbryoFromAnotherCouple.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.donatedEmbryoFromAnotherCouple.validator
                                    .$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
                v-if="hasUpdatePermission"
                :disabled="v$.$invalid"
            />

            <p-button
                icon="pi pi-check"
                label="Mark As Completed"
                @click="markAsComplete('fertilization')"
                class="p-ml-2"
            />
        </template>
    </p-card>
</template>

<script>
import { inject, onMounted, onUnmounted, toRefs, watch } from "vue";
import {
    useBornMarkAsComplete,
    useBronFrozenEmbyoInit
} from "@/composable/born";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { usePermissionCheck } from "@/composable/users";
import { emitter } from "@/event-bus";
export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            loading,
            form,
            v$,
            save,
            fillForm,
            embryoStageThawingOptions,
            embryoFrozenCryoMethodOptions
        } = useBronFrozenEmbyoInit();

        const { isInBornPage } = toRefs(props);

        watch(() => store.state.born.frozenEmbryos, fillForm);

        const bornForm = inject("bornForm", null);

        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.frozenEmbryo = {
                ...newValue,
                oocyteCollectionDate: form.oocyteCollectionDate
                    ? format(form.oocyteCollectionDate, DATE_FORMAT_STRING)
                    : null
            };
        });

        watch(
            () => form.utilizableAfterThaw,
            (newVal) => {
                store.commit("born/SET_UTILIZABLE_AFTER_THAW", newVal);
            }
        );

        watch(
            () => form.embryosThawed,
            (newVal) => {
                store.commit("born/SET_EMBRYOS_THAWED", newVal);
            }
        );

        watch(
            () => store.state.born.oocyteCollections,
            (newVal) => {
                store.commit(
                    "born/SET_OOCYTES_RETRIEVED",
                    newVal.retrievedOrThawed
                );
                store.commit(
                    "born/SET_OOCYTE_CRYO_PRESERVED",
                    newVal.oocytesCryopreserved
                );
            }
        );

        const { hasUpdatePermission } = usePermissionCheck();

        onMounted(() => {
            emitter.on("born:save-temp", save);

            if (
                store.state.artCycle.selectedCycle.cycleType == "FET" &&
                form.embryoFrozenCryoMethod == null
            ) {
                form.embryoFrozenCryoMethod = "ECRY002";
            }

            if (isInBornPage.value) {
                return;
            }
            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        const { markAsComplete } = useBornMarkAsComplete();

        return {
            isInBornPage,
            loading,
            form,
            v$,
            save,
            fillForm,
            embryoStageThawingOptions,
            embryoFrozenCryoMethodOptions,
            hasUpdatePermission,
            markAsComplete
        };
    }
};
</script>
