import { computed, inject, reactive } from "vue";
import { useStore } from "vuex";

export function useDialogEmbryoDetailForm() {
    const store = useStore();

    const selectedEmbryo = inject("selectedEmbryo");
    const embryo = inject("embryoDetail");

    const form = reactive({
        fertLineId: null,
        dayNo: 0,
        oocId: null,
        cultureDay: null,
        embryoAge: null,
        stage: null,
        grade: null,
        score: null,
        time: null,
        oocGroup: null,
        clinician: null,
        embryologist: null
    });

    const stageOptions = computed(() => {
        if (selectedEmbryo.day == 0) {
            return store.state.options.maturityList;
        } else if (selectedEmbryo.day == 1) {
            return store.state.options.fertDay1List;
        } else if (selectedEmbryo.day == 5) {
            return store.state.options.fertDay5List;
        } else if (selectedEmbryo.day == 6) {
            return store.state.options.fertDay5List;
        } else {
            return store.state.options.fertDay3List;
        }
    });

    const scoreOpts = computed(() => {
        if (selectedEmbryo.day == 0) {
            return store.state.options.fertCycleType;
        } else {
            return store.state.options.fertGrade;
        }
    });

    const clinicianOptions = computed(() => store.state.provider.providers);
    const embryologistOptions = computed(
        () => store.state.doctor.embryologists
    );

    return {
        store,
        embryo,
        selectedEmbryo,
        stageOptions,
        clinicianOptions,
        embryologistOptions,
        scoreOpts
    };
}
