<template>
    <div class="p-grid">
        <div class="p-fluid p-col-12 p-md-9">
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-6 ">
                    Ooc ID
                </label>
                <div class="p-col-12 p-md-6">
                    {{
                        selectedEmbryo.occId == 0 ? "New" : selectedEmbryo.occId
                    }}
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-6 ">
                    Culture Day
                </label>
                <div class="p-col-12 p-md-6">
                    {{ selectedEmbryo.date }}
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-6 ">
                    Embryo age [d]
                </label>
                <div class="p-col-12 p-md-6">
                    {{ embryo.embryoAge }}
                </div>
            </div>
        </div>
        <div class="p-col-12 p-md-3"></div>
    </div>

    <div class="p-grid">
        <div class="p-fluid p-col-12">
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    {{ selectedEmbryo.day == 0 ? "Maturity" : "Stage" }}
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.stage"
                        :options="stageOptions"
                        optionLabel="label"
                        optionValue="value"
                        :showClear="true"
                    />
                    <!-- 
                        :class="{
                            'p-invalid':
                                embryoDetailSubmitted &&
                                embryoDetailV$.stage.$invalid
                        }"
                        
                        <small
                        v-if="
                            embryoDetailV$.stage.$invalid &&
                                embryoDetailSubmitted
                        "
                        class="p-error"
                        >{{
                            embryoDetailV$.stage.required.$message.replace(
                                "Value",
                                "Straw ID"
                            )
                        }}
                    </small> -->
                </div>
            </div>
            <div class="p-field p-grid" v-if="selectedEmbryo.day != 1">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    {{ selectedEmbryo.day == 0 ? "Cycle Type" : "Grade" }}
                </label>
                <div class="p-col-12 p-md-3">
                    <p-dropdown
                        v-model="embryo.score"
                        :options="scoreOpts"
                        :showClear="true"
                        optionLabel="label"
                        optionValue="value"
                        v-if="
                            selectedEmbryo.day != 5 &&
                                selectedEmbryo.day != 6 &&
                                selectedEmbryo.day != 7
                        "
                    />
                    <p-input-text v-model="embryo.score" type="text" v-else />
                </div>

                <label class="p-col-12 p-mb-2 p-md-1 ">
                    Time
                </label>
                <div class="p-col-12 p-md-3">
                    <p-input-text v-model="embryo.time" type="text" />
                </div>
            </div>

            <div class="p-field p-grid" v-else>
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Time
                </label>
                <div class="p-col-12 p-md-3">
                    <p-input-text v-model="embryo.time" type="text" />
                </div>
            </div>

            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Clinician
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.clinician"
                        :options="clinicianOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Embryologist
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.embryologist"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Embryologist doing biopsy
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.embryologistDoingBiopsy"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Embryologist doing Freezing
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.embryologistDoingFreezing"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Embryologist doing Thawing
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.embryologistDoingThawing"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Embryologist doing Transfer
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.embryologistDoingTransfer"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Embryologist doing Scoring
                </label>
                <div class="p-col-12 p-md-7">
                    <p-dropdown
                        v-model="embryo.embryologistDoingScoring"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                        :filter="true"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid" v-if="selectedEmbryo.day == 3">
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Assisted Hatching
                </label>
                <div class="p-col-12 p-md-7">
                    <p-checkbox
                        v-model="embryo.assistedHatching"
                        :binary="true"
                    />
                </div>
            </div>
            <div
                class="p-field p-grid"
                v-if="
                    selectedEmbryo.day == 5 ||
                        selectedEmbryo.day == 6 ||
                        selectedEmbryo.day == 7
                "
            >
                <label class="p-col-12 p-mb-2 p-md-4 ">
                    Biopsy
                </label>
                <div class="p-col-12 p-md-7">
                    <p-checkbox v-model="embryo.biopsy" :binary="true" />
                </div>
            </div>

            <div class="p-grid">
                <div class="p-md-4 p-field-checkbox">
                    <p-checkbox
                        v-model="embryo.asphericalForm"
                        :binary="true"
                    />
                    <label for="hcgiu">Aspherical Form</label>
                </div>
                <div class="p-md-4 p-field-checkbox">
                    <p-checkbox v-model="embryo.vacuolisation" :binary="true" />
                    <label for="hcgiu">Vacoulisation</label>
                </div>
                <div class="p-md-4 p-field-checkbox">
                    <p-checkbox
                        v-model="embryo.granularCytoplasm"
                        :binary="true"
                    />
                    <label for="hcgiu">Granular Cytoplasm</label>
                </div>
                <div class="p-md-4 p-field-checkbox">
                    <p-checkbox v-model="embryo.abnormalZona" :binary="true" />
                    <label for="hcgiu">Abnormal Zona</label>
                </div>
                <div class="p-md-4 p-field-checkbox">
                    <p-checkbox
                        v-model="embryo.fragmentedPolarBody"
                        :binary="true"
                    />
                    <label for="hcgiu">Fragmented Polar Body</label>
                </div>
                <div class="p-md-4 p-field-checkbox">
                    <p-checkbox
                        v-model="embryo.fragmentsInPus"
                        :binary="true"
                    />
                    <label for="hcgiu">Frangmented in pus</label>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { useDialogEmbryoDetailForm } from "@/composable/fertilzation-embryo-dialog";
import { computed, inject } from "vue-demi";

export default {
    setup() {
        const {
            store,
            embryo,
            stageOptions,
            selectedEmbryo,
            clinicianOptions,
            embryologistOptions,
            scoreOpts
        } = useDialogEmbryoDetailForm();

        const embryoDetailV$ = inject("embryoDetailV$");
        const embryoDetailSubmitted = inject("embryoDetailSubmitted");

        const fateOptions = computed(() => store.state.options.fateList);

        return {
            embryo,
            selectedEmbryo,
            stageOptions,
            clinicianOptions,
            embryologistOptions,
            scoreOpts,
            fateOptions,
            embryoDetailV$,
            embryoDetailSubmitted
        };
    }
};
</script>

<style scoped>
.p-field-checkbox {
    margin-bottom: 0;
}
</style>
