<template>
    <div class="p-grid">
        <div class="p-col">
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Incubator
                </label>
                <div class="p-col-fixed" style="width: 200px">
                    <p-input-text
                        v-model="embryo.incubator"
                        style="width: 100%"
                        v-if="selectedEmbryo.day != 0"
                    />
                    <p-dropdown
                        v-model="embryo.incubator"
                        style="width: 100%"
                        :options="incubatorOptions"
                        v-else
                    />
                </div>
            </div>
            <!-- <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 100px">
                    Comment
                </label>
                <div class="p-col">
                    <p-textarea
                        v-model="embryo.description"
                        rows="5"
                        style="width: 100%"
                    />
                </div>
            </div> -->
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Upload Image
                </label>
                <div class="p-col">
                    <p-file-upload
                        v-if="uploaded"
                        name="demo[]"
                        mode="basic"
                        :customUpload="true"
                        accept="image/*"
                        :auto="true"
                        @uploader="imgUploader"
                    ></p-file-upload>
                </div>
            </div>

            <div class="p-field p-grid">
                <label class="p-col" style="color:red; padding-top: 40px;">
                    {{ selectedEmbryo.errorMsg }}
                </label>
            </div>

            <!-- <div class="p-grid">
                <div class="p-md-6">
                    <div class="p-inputgroup p-inputtext-sm flex-1">
                        <p-input-text
                            v-model="embryo.freezer"
                            class=""
                            placeholder="Keyword"
                        />
                        <p-button
                            label="..."
                            @click="showFreezerDialog"
                        ></p-button>
                    </div>
                </div>
            </div> -->
        </div>

        <div class="p-col-fixed" style="width: 200px">
            <img :src="image" alt="image" style="width: 150px; height: 150px" />
        </div>

        <div
            class="p-col-fixed"
            style="
                justify-content: end;
                display: flex;
            "
        >
            <table style="max-height: 100px">
                <tr>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-up"
                            @click="onArrowBtnClick('up')"
                        />
                    </td>
                    <td></td>
                </tr>
                <tr>
                    <td>
                        <p-button
                            icon="pi pi-angle-left"
                            @click="onArrowBtnClick('left')"
                        />
                    </td>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-right"
                            @click="onArrowBtnClick('right')"
                        />
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-down"
                            @click="onArrowBtnClick('down')"
                        />
                    </td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import { inject, onMounted, ref, watch } from "vue";
import { emitter } from "@/event-bus";
import { ORIGIN } from "@/config";

export default {
    setup() {
        const embryo = inject("embryoDetail");
        const selectedEmbryo = inject("selectedEmbryo");

        const image = ref("https://via.placeholder.com/300");
        const uploaded = ref(true);
        const imgUploader = (event) => {
            uploaded.value = false;
            const reader = new FileReader();
            reader.readAsDataURL(event.files[0]);
            reader.onload = () => {
                embryo.newImage = reader.result;
                image.value = reader.result;
                uploaded.value = true;
            };
            reader.onerror = (error) => console.log(error);
        };

        const freezerDailog = inject("freezerDailog");

        watch(
            () => embryo.images,
            (val) => {
                if (val.length == 0) {
                    image.value = "https://via.placeholder.com/300";
                } else {
                    image.value = `${ORIGIN}/${val[val.length - 1]}`;
                }
            }
        );

        const onArrowBtnClick = (direction) => {
            emitter.emit("embyro-detail-dialog:move-cell", direction);
        };

        const incubatorOptions = [
            "Minc 1",
            "Minc 2",
            "Minc 3",
            "Planer 1",
            "Planer 2",
            "Planer 3",
            "Planer 4",
            "Ksystem 1",
            "Ksystem 2"
        ];

        onMounted(() => {
            if (embryo.images.length == 0) {
                image.value = "https://via.placeholder.com/300";
            } else {
                image.value = `${ORIGIN}/${
                    embryo.images[embryo.images.length - 1]
                }`;
            }
        });

        return {
            embryo,
            selectedEmbryo,
            image,
            onArrowBtnClick,
            imgUploader,
            uploaded,
            incubatorOptions,
            showFreezerDialog: () => (freezerDailog.value = true)
        };
    }
};
</script>
