<template>
    <p-dialog
        v-model:visible="embryoDetailDialog"
        :style="{ width: '1200px' }"
        :modal="true"
        class="p-inputtext-sm"
        @show="registerOnArrowKeyMovement"
        @hide="unregisterOnArrowKeyMovement"
    >
        <template #header>
            <div>
                <h3>
                    Oocytes / Embryo Detail
                    <spn style="margin-left: 150px;">
                        (Day {{ selectedEmbryo.day }})
                    </spn>
                </h3>
            </div>
        </template>

        <div class="p-grid">
            <div class="p-col-12 p-md-6">
                <embryo-detail-form />
            </div>

            <div class="p-col-12 p-md-6">
                <embryo-detail-cells-form />
                <embryo-detail-cyro-storage-form />
            </div>
        </div>

        <p-tab-view ref="embryoTabView1">
            <!-- <p-tab-panel header="Files">
                <embryo-detail-files-form />
            </p-tab-panel>
            <p-tab-panel header="Assisted Hatching">
                <embryo-detail-assisted-hatching-form />
            </p-tab-panel>
            <p-tab-panel header="Polar body diagnosis">
                <embryo-detail-polar-body-diagnosis-form />
            </p-tab-panel>
            <p-tab-panel header="PGD">
                <embryo-detail-pgd-form />
            </p-tab-panel> -->
            <p-tab-panel header="Incubator">
                <embryo-detail-incubator-form />
            </p-tab-panel>
        </p-tab-view>
        <!-- </p-block-ui> -->

        <template #footer>
            <p-button
                label="Close"
                icon="pi pi-times"
                class="p-button-outlined p-button-danger"
                @click="closeEmbryoDetailDialog"
            />
            <p-button
                label="Save"
                icon="pi pi-check"
                @click="saveEmbryoDetail"
                :loading="loading"
                v-if="hasUpdatePermission"
            />
        </template>

        <embryo-detail-frezzer-dialog @freezer-select="freezerSelectHandler" />
    </p-dialog>
</template>

<script>
import { useEmbryoDetailDialogInit } from "@/composable/fertilization";
import { provide, ref } from "vue";
import { emitter } from "@/event-bus";

import EmbryoDetailForm from "@/components/fertilization/embryo-detail-form.vue";
import EmbryoDetailFilesForm from "@/components/fertilization/embryo-detail-files-form.vue";
import EmbryoDetailIncubatorForm from "@/components/fertilization/embryo-detail-incubator-form.vue";
import EmbryoDetailCellsForm from "@/components/fertilization/embryo-detail-cells-form.vue";
import EmbryoDetailAssistedHatchingForm from "@/components/fertilization/embryo-detail-assited-hatching-form.vue";
import EmbryoDetailPolarBodyDiagnosisForm from "@/components/fertilization/embryo-detail-polar-body-diagnosis-form.vue";
import EmbryoDetailPgdForm from "@/components/fertilization/embryo-detail-pgd-form.vue";
import EmbryoDetailDimensionsForm from "@/components/fertilization/embryo-detail-dimensions-form.vue";
import EmbryoDetailCyroStorageForm from "@/components/fertilization/cryo-storage-form.vue";
import EmbryoDetailFrezzerDialog from "@/components/fertilization/freezer-selection-dialog.vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        EmbryoDetailForm,
        EmbryoDetailFilesForm,
        EmbryoDetailIncubatorForm,
        EmbryoDetailCellsForm,
        EmbryoDetailAssistedHatchingForm,
        EmbryoDetailPolarBodyDiagnosisForm,
        EmbryoDetailPgdForm,
        EmbryoDetailDimensionsForm,
        EmbryoDetailCyroStorageForm,
        EmbryoDetailFrezzerDialog
    },
    setup() {
        const {
            embryoDetailDialog,
            closeEmbryoDetailDialog,
            embryoDetail,
            selectedEmbryo,
            saveEmbryoDetail,
            getEmbryoDetail,
            selectedFreezer,
            loading,
            cyroStorage,
            cyroStorageV$,
            cyroStorageSubmitted
        } = useEmbryoDetailDialogInit();

        const { hasUpdatePermission } = usePermissionCheck();

        provide("embryoDetail", embryoDetail);

        provide("cyroStorage", cyroStorage);
        provide("cyroStorageV$", cyroStorageV$);
        provide("cyroStorageSubmitted", cyroStorageSubmitted);

        const freezerDailog = ref(false);
        provide("freezerDailog", freezerDailog);

        provide("selectedFreezer", selectedFreezer);

        const moveCell = (e) => {
            switch (e.keyCode) {
                case 37:
                    emitter.emit("embyro-detail-dialog:move-cell", "left");
                    break;
                case 38:
                    emitter.emit("embyro-detail-dialog:move-cell", "up");
                    break;
                case 39:
                    emitter.emit("embyro-detail-dialog:move-cell", "right");
                    break;
                case 40:
                    emitter.emit("embyro-detail-dialog:move-cell", "down");
                    break;
            }
        };

        const registerOnArrowKeyMovement = () => {
            document.addEventListener("keydown", moveCell);
        };

        const unregisterOnArrowKeyMovement = () => {
            document.removeEventListener("keydown", moveCell);
        };

        const freezerSelectHandler = (key) => {
            cyroStorage.location = key;
            freezerDailog.value = false;
        };

        return {
            embryoDetailDialog,
            closeEmbryoDetailDialog,
            selectedEmbryo,
            saveEmbryoDetail,
            getEmbryoDetail,
            loading,
            registerOnArrowKeyMovement,
            unregisterOnArrowKeyMovement,
            hasUpdatePermission,
            freezerSelectHandler
        };
    }
};
</script>

<style scoped>
:deep() .p-inputnumber,
:deep() .p-calendar {
    height: 24.5px;
}
.p-tabview :deep() .p-tabview-nav li .p-tabview-nav-link {
    padding: 0.5rem;
}

.p-tabview :deep() .p-tabview-panels {
    padding: 0.5rem;
}

.p-tabview :deep() .p-tabview-nav {
    /* justify-content: space-evenly; */
}
</style>
