<template>
    <div class="p-formgroup-inline p-mb-2">
        <div class="p-field">
            <label>Embryo From</label>
            <p-dropdown
                v-model="form.primaryCycleId"
                :class="{
                    'p-invalid': v$.primaryCycleId.$invalid
                }"
                :options="primaryCycles"
                optionLabel="label"
                optionValue="cycleId"
                @change="onEmbyroAssingedToChageHandler"
            />
            <i
                v-if="v$.primaryCycleId.$invalid"
                class="pi pi-info-circle p-mt-2 p-ml-2"
                v-tooltip.right="v$.primaryCycleId.validator.$message"
                style="color: #f44336"
            ></i>
        </div>

        <div class="p-field">
            <label>Donor Patient Chart #</label>
            <!-- <p-dropdown
                v-model="form.otherPatientId"
                :options="[{ cycleId: 122 }]"
                optionLabel="cycleId"
                optionValue="cycleId"
            /> -->
            <p-auto-complete
                v-model="form.otherPatient"
                :suggestions="filteredPatient"
                field="label"
                placeholder="Type to search"
                @complete="patientFilter"
                @item-select="getOtherPrimaryCycles"
                optionLabel="label"
                optionValue="demographicNo"
            />
        </div>

        <div class="p-field">
            <label>Cycle ID</label>
            <p-dropdown
                v-model="form.otherPrimaryCycleId"
                :options="otherPrimaryCycles"
                optionLabel="label"
                optionValue="cycleId"
                @change="onOtherCycleIdChangeHandler"
            />
        </div>

        <div class="p-field-checkbox p-mt-1">
            <p-checkbox v-model="form.fromOutside" :binary="true" />
            <label>From outside</label>
        </div>
    </div>
</template>

<script>
import { useEbmryoInfo } from "@/composable/fertilization";
import { onMounted, onUnmounted, watch } from "vue-demi";
import { emitter } from "@/event-bus";
import { getPatient } from "@/service/patient-message-log";
export default {
    setup() {
        const {
            store,
            form,
            v$,
            save,
            primaryCycles,
            filteredPatient,
            patientFilter,
            otherPrimaryCycles,
            getOtherPrimaryCycles,
            onOtherCycleIdChangeHandler,
            onEmbyroAssingedToChageHandler
        } = useEbmryoInfo();

        watch(
            () => store.state.born.primaryCycleInfo,
            (newVal) => {
                form.primaryCycleId = newVal.primaryCycleId;
                // form.otherPatientId = newVal.otherPatientId;
                form.otherPrimaryCycleId = newVal.otherPrimaryCycleId;
                form.fromOutside = newVal.fromOutside == 1 ? true : false;
                getPatient(newVal.otherPatientId)
                    .then((data) => {
                        let patient = {
                            demographicNo: data.demographic_no,
                            firstName: data.first_name,
                            lastName: data.last_name,
                            address: data.address,
                            city: data.city,
                            postal: data.postal,
                            phone: data.phone,
                            dateOfBirth: `${data.year_of_birth}-${data.month_of_birth}-${data.date_of_birth}`,
                            providerNo: data.provider_no,
                            label: `${data.demographic_no} - ${data.first_name} ${data.last_name}`
                        };

                        form.otherPatient = patient;
                        getOtherPrimaryCycles();
                    })
                    .catch((err) => {});
            }
        );

        onMounted(() => {
            store.dispatch("fertilization/getPrimaryCycles");
            emitter.on("fresh-embryo:on-save", save);
        });

        onUnmounted(() => {
            emitter.off("fresh-embryo:on-save", save);
        });

        return {
            form,
            v$,
            primaryCycles,
            filteredPatient,
            patientFilter,
            otherPrimaryCycles,
            getOtherPrimaryCycles,
            onOtherCycleIdChangeHandler,
            onEmbyroAssingedToChageHandler
        };
    }
};
</script>
