<template>
    <p-dialog
        header="Freezer Selection"
        v-model:visible="freezerDailog"
        :style="{ width: '500px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
        @show="onShow"
    >
        <!-- <div class="p-field" style="margin-bottom: 10px;">
            <label class="p-mr-4">Freezer</label>
            <p-dropdown
                v-model="equipment"
                :options="freezerEquipments"
                optionLabel="description"
                @change="frrezerChageHanler"
            />
        </div> -->

        <p-progress-bar
            v-if="loading"
            mode="indeterminate"
            style="height: .5em"
        />

        <p-tree
            :value="freezerItems"
            selectionMode="single"
            @nodeExpand="loadItems"
            @nodeSelect="onNodeSelect"
        >
            <template #default="slotProps">
                <b>{{ slotProps.node.label }}</b>
            </template>
            <template #leaf="slotProps">
                <label style="color:blue; cursor: pointer;">{{
                    slotProps.node.label
                }}</label>
            </template>
        </p-tree>
    </p-dialog>
</template>

<script>
import { computed, inject, ref, onMounted } from "vue";
import { useStore } from "vuex";

const nest = (items, key = null, link = "parent") =>
    items
        .filter((item) => item[link] === key)
        .map((item) => ({ ...item, children: nest(items, item.key) }));

export default {
    emits: ["freezer-select"],
    setup(props, { emit }) {
        const store = useStore();
        const freezerDailog = inject("freezerDailog");
        const loading = ref(false);

        const equipment = ref();

        const selectedFreezer = inject("selectedFreezer");

        const freezerEquipments = computed(
            () => store.state.fertilization.freezerEquipments
        );

        const freezerItems = computed(() =>
            nest(store.state.fertilization.freezerItems)
        );

        const frrezerChageHanler = (event) => {
            loading.value = true;
            store
                .dispatch("fertilization/getFreezerItems", {
                    eId: selectedFreezer.value?.eId,
                    iType: selectedFreezer.value?.eType,
                    separator: "/",
                    parentKey: null
                })
                .finally(() => {
                    loading.value = false;
                });
        };

        const cryoStorage = inject("cyroStorage");

        const onShow = () => {
            let index = freezerEquipments.value.findIndex(
                (item) => item.eId == cryoStorage.freezerId
            );
            if (index >= 0) {
                selectedFreezer.value = freezerEquipments.value[index];

                loading.value = true;
                store
                    .dispatch("fertilization/getFreezerItems", {
                        eId: selectedFreezer.value?.eId,
                        iType: selectedFreezer.value?.eType,
                        separator: "/",
                        parentKey: null
                    })
                    .finally(() => {
                        loading.value = false;
                    });
            }
        };

        const loadItems = (e) => {
            loading.value = true;
            store
                .dispatch("fertilization/getFreezerItems", {
                    eId: selectedFreezer.value?.eId,
                    iType: selectedFreezer.value?.eType,
                    separator: "/",
                    parentKey: e.key
                })
                .finally(() => {
                    loading.value = false;
                });
        };

        onMounted(() => {
            //store.dispatch("fertilization/getAllFreezers");
        });

        return {
            freezerDailog,
            freezerEquipments,
            equipment,
            freezerItems,
            frrezerChageHanler,
            loadItems,
            loading,
            onNodeSelect: (node) => {
                if (node.leaf) {
                    emit("freezer-select", node.key);
                }
            },
            onShow
        };
    }
};
</script>
