<template>
    <p-card class="p-mt-1 fresh-embryo">
        <template #content>
            <embryo-info v-if="isFETcycle" />

            <p-divider align="left" class="p-mt-0">
                <b>Embryology - Fresh Embryo</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Insemination Method
                        </label>
                        <div class="p-col">
                            <p-multi-select
                                v-model="form.inseminationMethod"
                                :options="inseminationMethodOptions"
                                optionLabel="label"
                                optionValue="value"
                                style="width: 250px"
                                :class="{
                                    'p-invalid': v$.inseminationMethod.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.inseminationMethod.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.inseminationMethod.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # oocytes not used:
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.oocytesNotUsed"
                                :max="99"
                                :class="{
                                    'p-invalid': v$.oocytesNotUsed.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.oocytesNotUsed.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.oocytesNotUsed.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Reason for no use
                        </label>
                        <div class="p-col">
                            <p-multi-select
                                v-model="form.reasonsForNoUse"
                                :options="reasonsForNoUseOptions"
                                optionLabel="label"
                                optionValue="value"
                                style="width: 250px"
                                :class="{
                                    'p-invalid': v$.reasonsForNoUse.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.reasonsForNoUse.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.reasonsForNoUse.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo stage at freezing
                        </label>
                        <div class="p-col">
                            <p-multi-select
                                v-model="form.embryoStageFreezing"
                                :options="embryoStageFreezingOptions"
                                optionLabel="label"
                                optionValue="value"
                                style="width: 250px"
                                :class="{
                                    'p-invalid': v$.embryoStageFreezing.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoStageFreezing.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoStageFreezing.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>

                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # oocytes inseminated/injected
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.inseminatedInjected"
                                :max="99"
                                :class="{
                                    'p-invalid': v$.inseminatedInjected.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.inseminatedInjected.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.inseminatedInjected.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Freeze ID #1
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.embryoFreezeID1" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Freeze ID #2
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.embryoFreezeID2" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Freeze ID #3
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.embryoFreezeID3" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embyro Freeze ID #4
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.embryoFreezeID4" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # with Normal Fertilization (2PN)
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.normalFertilization2PN"
                                :max="99"
                                :class="{
                                    'p-invalid':
                                        v$.normalFertilization2PN.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.normalFertilization2PN.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.normalFertilization2PN.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Frezze Date 1
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.embryoFreezeDate1"
                                dateFormat="mm/dd/yy"
                                :showIcon="true"
                                :class="{
                                    'p-invalid': v$.embryoFreezeDate1.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoFreezeDate1.validator.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFreezeDate1.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                        <i
                            v-if="v$.embryoFreezeDate1.validDate.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFreezeDate1.validDate.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Frezze Date 2
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.embryoFreezeDate2"
                                dateFormat="mm/dd/yy"
                                :showIcon="true"
                                :class="{
                                    'p-invalid': v$.embryoFreezeDate2.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoFreezeDate2.validDate.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFreezeDate2.validDate.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Frezze Date 3
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.embryoFreezeDate3"
                                dateFormat="mm/dd/yy"
                                :showIcon="true"
                                :class="{
                                    'p-invalid': v$.embryoFreezeDate3.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoFreezeDate3.validDate.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFreezeDate3.validDate.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Frezze Date 4
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.embryoFreezeDate4"
                                dateFormat="mm/dd/yy"
                                :showIcon="true"
                                :class="{
                                    'p-invalid': v$.embryoFreezeDate4.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoFreezeDate4.validDate.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFreezeDate4.validDate.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            #of MII oocytes
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.ofMII"
                                :max="99"
                                :class="{
                                    'p-invalid': v$.ofMII.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.ofMII.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="v$.ofMII.validator.$message"
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # utilizable embryos
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.utilizableEmbryos"
                                :max="99"
                                :class="{
                                    'p-invalid': v$.utilizableEmbryos.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.utilizableEmbryos.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.utilizableEmbryos.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # embryos cryopreserved
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.embryosCryopreserved"
                                :max="99"
                                :class="{
                                    'p-invalid':
                                        v$.embryosCryopreserved.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryosCryopreserved.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryosCryopreserved.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Reason for embryo cryopreservation
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.reasonEmbryoCryopreservation"
                                :options="reasonEmbryoCryopreservationOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid':
                                        v$.reasonEmbryoCryopreservation.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.reasonEmbryoCryopreservation.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.reasonEmbryoCryopreservation.validator
                                    .$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo Cryo method
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.embryoFreshCryoMethod"
                                :options="embryoFreshCryoMethodOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid':
                                        v$.embryoFreshCryoMethod.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.embryoFreshCryoMethod.$invalid"
                            class="pi pi-info-circle p-mt-2"
                            v-tooltip.right="
                                v$.embryoFreshCryoMethod.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
                v-if="hasUpdatePermission"
                :disabled="v$.$invalid"
            />
        </template>
    </p-card>
</template>

<script>
import { useBornFreshEmbryoInit } from "@/composable/born";
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    toRefs,
    watch
} from "vue-demi";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { usePermissionCheck } from "@/composable/users";
import { emitter } from "@/event-bus";

import EmbryoInfo from "@/components/born/embryo-info.vue";

export default {
    components: {
        EmbryoInfo
    },
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            loading,
            form,
            v$,
            fillForm,
            save,
            inseminationMethodOptions,
            reasonsForNoUseOptions,
            embryoStageFreezingOptions,
            reasonEmbryoCryopreservationOptions,
            embryoFreshCryoMethodOptions,
            cycleIds,
            getDeafultDataByPrimaryCycle
        } = useBornFreshEmbryoInit();

        const { isInBornPage } = toRefs(props);

        watch(() => store.state.born.freshEmbryos, fillForm);

        watch(
            () => form.inseminatedInjected,
            (newVal) => {
                store.commit("born/SET_OOCYTES_INSEMINATED_INJECTED", newVal);
            }
        );

        const bornForm = inject("bornForm", null);

        watch(
            () => [
                store.state.born.dependentOn.oocytesRetrieved,
                form.inseminatedInjected,
                store.state.born.dependentOn.oocytesCryopreserved
            ],
            ([oocytesRetrieved, inseminatedInjected, oocytesCryopreserved]) => {
                if (form.ofMII == null) {
                    form.ofMII = inseminatedInjected;
                }

                if (store.state.artCycle.selectedCycle.cycleType == "FET")
                    return;

                if (
                    oocytesRetrieved != null &&
                    inseminatedInjected != null &&
                    oocytesCryopreserved != null
                ) {
                    form.oocytesNotUsed =
                        oocytesRetrieved -
                        inseminatedInjected -
                        oocytesCryopreserved;
                }
            }
        );

        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.freshEmbryo = {
                ...newValue,
                embryoFreezeDate1: form.embryoFreezeDate1
                    ? format(
                          new Date(form.embryoFreezeDate1),
                          DATE_FORMAT_STRING
                      )
                    : null,
                embryoFreezeDate2: form.embryoFreezeDate2
                    ? format(
                          new Date(form.embryoFreezeDate2),
                          DATE_FORMAT_STRING
                      )
                    : null,
                embryoFreezeDate3: form.embryoFreezeDate3
                    ? format(
                          new Date(form.embryoFreezeDate3),
                          DATE_FORMAT_STRING
                      )
                    : null,
                embryoFreezeDate4: form.embryoFreezeDate4
                    ? format(
                          new Date(form.embryoFreezeDate4),
                          DATE_FORMAT_STRING
                      )
                    : null
            };
        });

        watch(
            () => [form.utilizableEmbryos, form.embryosCryopreserved],
            ([utilizableEmbryos, embryosCryopreserved]) => {
                store.commit("born/SET_UTILIZABLE_EMBRYOS", utilizableEmbryos);
                store.commit(
                    "born/SET_EMBRYOS_CRYOPRESERVED",
                    embryosCryopreserved
                );
            }
        );

        const isFETcycle = computed(
            () => store.state.artCycle.selectedCycle.cycleType == "FET"
        );

        const { hasUpdatePermission } = usePermissionCheck();

        onMounted(() => {
            emitter.on("born:save-temp", save);
            emitter.on(
                "embryo-info:assigned-to-change",
                getDeafultDataByPrimaryCycle
            );

            if (isInBornPage.value) {
                return;
            }
            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
            emitter.off(
                "embryo-info:assigned-to-change",
                getDeafultDataByPrimaryCycle
            );
        });

        return {
            isInBornPage,
            loading,
            form,
            v$,
            save,
            inseminationMethodOptions,
            reasonsForNoUseOptions,
            embryoStageFreezingOptions,
            reasonEmbryoCryopreservationOptions,
            embryoFreshCryoMethodOptions,
            hasUpdatePermission,
            cycleIds,
            isFETcycle
        };
    }
};
</script>
